import React from "react";
import { injectIntl } from "react-intl";
import { withRouter } from "react-router-dom";
import { Row, Col, Icon, Tooltip, Card } from "antd";
import { ChartCard, Field, MiniArea, MiniBar, MiniProgress, Bar, Pie, yuan } from 'ant-design-pro/lib/Charts';
import Trend from 'ant-design-pro/lib/Trend';
import NumberInfo from 'ant-design-pro/lib/NumberInfo';

import numeral from 'numeral';
import moment from 'moment';
import styled from 'styled-components';


const ColWrapper = styled(Col)`
  padding-left: 12px;
  padding-right: 12px;
  margin-bottom: 24px;
`


const DashboardCmp = (props) => {

  // const {
  //   intl: { messages }
  // } = props;

  const visitData = [];
  const beginDay = new Date().getTime();
  for (let i = 0; i < 20; i += 1) {
    visitData.push({
      x: moment(new Date(beginDay + 1000 * 60 * 60 * 24 * i)).format('YYYY-MM-DD'),
      y: Math.floor(Math.random() * 100) + 10,
    });
  }

  const salesData = [];
  for (let i = 0; i < 12; i += 1) {
    salesData.push({
      x: `${i + 1}月`,
      y: Math.floor(Math.random() * 1000) + 200,
    });
  }

  const salesPieData = [
    {
      x: '家用电器',
      y: 4544,
    },
    {
      x: '食用酒水',
      y: 3321,
    },
    {
      x: '个护健康',
      y: 3113,
    },
    {
      x: '服饰箱包',
      y: 2341,
    },
    {
      x: '母婴产品',
      y: 1231,
    },
    {
      x: '其他',
      y: 1231,
    },
  ];

  return (
    <div>
      <Row style={{ margin: '12px -12px 0 -12px' }}>
        <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
          <ChartCard title="搜索用户数量" total={numeral(8846).format('0,0')} contentHeight={134}>
            <NumberInfo
              subTitle={<span>本周访问</span>}
              total={numeral(12321).format('0,0')}
              status="up"
              subTotal={17.1}
            />
            <MiniArea line height={45} data={visitData} />
          </ChartCard>
        </ColWrapper>
        <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
          <ChartCard
            title="访问量"
            action={
              <Tooltip title="指标说明">
                <Icon type="info-circle-o" />
              </Tooltip>
            }
            total={numeral(8846).format('0,0')}
            footer={<Field label="日访问量" value={numeral(1234).format('0,0')} />}
            contentHeight={46}
          >
            <MiniBar height={46} data={visitData} />
          </ChartCard>
        </ColWrapper>
        <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
          <ChartCard
            title="线上购物转化率"
            action={
              <Tooltip title="指标说明">
                <Icon type="info-circle-o" />
              </Tooltip>
            }
            total="78%"
            footer={
              <div>
                <span>
                  周同比
              <Trend flag="up" style={{ marginLeft: 8, color: 'rgba(0,0,0,.85)' }}>
                    12%
              </Trend>
                </span>
                <span style={{ marginLeft: 16 }}>
                  日环比
              <Trend flag="down" style={{ marginLeft: 8, color: 'rgba(0,0,0,.85)' }}>
                    11%
              </Trend>
                </span>
              </div>
            }
            contentHeight={46}
          >
            <MiniProgress percent={78} strokeWidth={8} target={80} />
          </ChartCard>
        </ColWrapper>
        <ColWrapper xs={24} sm={12} md={12} lg={12} xl={6}>
          <ChartCard title="搜索用户数量" total={numeral(8846).format('0,0')} contentHeight={134}>
            <NumberInfo
              subTitle={<span>本周访问</span>}
              total={numeral(12321).format('0,0')}
              status="up"
              subTotal={17.1}
            />
            <MiniArea line height={45} data={visitData} />
          </ChartCard>
        </ColWrapper>
      </Row>
      <Row>
        <ColWrapper xs={24} sm={14} md={14} lg={14} xl={14}>
          <Card style={{ height: 350 }}>
            <Bar height={200} title="销售额趋势" data={salesData} />
          </Card>
        </ColWrapper>
        <ColWrapper xs={24} sm={10} md={10} lg={10} xl={10}>
          <Card>
            <Pie
              hasLegend
              title="销售额"
              subTitle="销售额"
              total={() => (
                <span
                  dangerouslySetInnerHTML={{
                    __html: yuan(salesPieData.reduce((pre, now) => now.y + pre, 0)),
                  }}
                />
              )}
              data={salesPieData}
              valueFormat={val => <span dangerouslySetInnerHTML={{ __html: yuan(val) }} />}
              height={294}
            />
          </Card>
        </ColWrapper>
      </Row>
    </div>
  );
}

export default withRouter(injectIntl(DashboardCmp));
